'use client'

import { useState, useEffect } from 'react'

const links = [
  { label: 'Tjänster', href: '#tjanster' },
  { label: 'Projekt',  href: '#projekt'  },
  { label: 'Om mig',  href: '#om-mig'   },
  { label: 'Kontakt', href: '#kontakt'  },
]

export default function Navbar() {
  const [open, setOpen] = useState(false)
  const [scrolled, setScrolled] = useState(false)

  useEffect(() => {
    const handler = () => setScrolled(window.scrollY > 20)
    window.addEventListener('scroll', handler)
    return () => window.removeEventListener('scroll', handler)
  }, [])

  return (
    <header
      className={`fixed top-0 inset-x-0 z-50 transition-shadow duration-200 bg-white ${
        scrolled ? 'shadow-sm' : ''
      }`}
    >
      <div className="max-w-content mx-auto px-5 h-16 flex items-center justify-between">
        <a href="#" className="font-bold text-lg text-brand-700 tracking-tight">
          Richard Oelschlager
        </a>

        <nav className="hidden md:flex items-center gap-8">
          {links.map(l => (
            <a
              key={l.href}
              href={l.href}
              className="text-sm font-medium text-muted hover:text-brand-600 transition-colors"
            >
              {l.label}
            </a>
          ))}
          <a
            href="#kontakt"
            className="text-sm font-semibold bg-brand-500 text-white px-4 py-2 rounded-lg hover:bg-brand-600 transition-colors"
          >
            Gratis konsultation
          </a>
        </nav>

        <button
          className="md:hidden p-2 text-ink"
          onClick={() => setOpen(!open)}
          aria-label="Toggle menu"
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            {open
              ? <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              : <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            }
          </svg>
        </button>
      </div>

      {open && (
        <nav className="md:hidden bg-white border-t border-slate-100 px-5 py-4 flex flex-col gap-4">
          {links.map(l => (
            <a
              key={l.href}
              href={l.href}
              onClick={() => setOpen(false)}
              className="text-base font-medium text-ink hover:text-brand-600"
            >
              {l.label}
            </a>
          ))}
          <a
            href="#kontakt"
            onClick={() => setOpen(false)}
            className="text-center font-semibold bg-brand-500 text-white px-4 py-3 rounded-lg"
          >
            Gratis konsultation
          </a>
        </nav>
      )}
    </header>
  )
}
